import Vue from 'vue'
import VueRouter from 'vue-router'
const Album = () => import('@/views/album/Album.vue')
const Albums = () => import('@/views/albums/Albums.vue')
const Artist = () => import('@/views/artist/Artist.vue')
const Artists = () => import('@/views/artists/Artists.vue')
const Home = () => import('@/views/home/Home.vue')
const Layout = () => import('@/views/Layout.vue')
const Playlist = () => import('@/views/playlist/Playlist.vue')
const Playlists = () => import('@/views/playlists/Playlists.vue')
const Search = () => import('@/views/search/Search.vue')
const Toplists = () => import('@/views/toplists/Toplists.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Layout,
    children: [
      { path: '/', component: Home },
      { path: 'album/:id', component: Album, props: true },
      { path: 'albums', component: Albums },
      { path: 'artist/:id', component: Artist, props: true },
      { path: 'artists', component: Artists },
      { path: 'playlists', component: Playlists },
      { path: 'playlist/:id', component: Playlist, props: true },
      { path: 'search/:keywords', component: Search, props: true },
      { path: 'toplists', component: Toplists }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
